import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import parser from "@/components/jyf-parser/jyf-parser";
import util from "../../static/js/util.js"
export default {
	name: "",
	components: {
		uniSearchBar,
		"jyf-parser": parser,
	},
	props: {},
	data() {
		return {
			initialVal: "",
			keyword: "",
			hotKeywordList: [],
			historyData: [],
			num: 0,
			searchResult: [],
			util: util.keyWord
		}
	},
	methods: {
		// 搜索框收入
		input(e) {
			this.num++
			if (this.keyword === e.value && this.num === 1) {
				return
			}
			this.keyword = e.value
			if (this.num <= 1) {
				return
			}
			let val = e.value.trim()
			if (val.length > 0) {
				this.$api.getSearch(val).then(res => {
					if (res.status === 200) {
						if (res.data.keywords.length === 0) {
							uni.showToast({
								title: "暂无相关数据",
								icon: "none"
							})
						} else {
							this.searchResult = res.data.keywords
						}
					}
				}).catch(err => {
					console.log(err)
				})
			} else {
				this.searchResult = []
			}
		},
		// 添加搜索
		addSearch(e) {
			this.initialVal = e
		},
		// 跳转到详情页
		details(id) {
			uni.navigateTo({
				url: `/pages/details/details?id=${id}`
			});
		},
		// 删除搜索历史
		delHistory() {
			uni.showModal({
				title: '提醒',
				content: '是否确认删除搜索历史？',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		// 返回
		back() {

			uni.navigateBack({
				delta: 1
			});






		}
	},
	mounted() {

	},
	onLoad() {
		let a=this.$store.state.indexaction
		if(a.defaultKeyword){
			this.initialVal = a.defaultKeyword.keyword
			this.hotKeywordList = a.hotKeywordList
			this.historyData = a.historyData
		}	
	},
	filters: {

	},
	computed: {

	},
	watch: {
		// "$store.state.indexaction"(val) {
		// 	this.initialVal = val.defaultKeyword.keyword
		// 	this.hotKeywordList = val.hotKeywordList
		// 	this.historyData = val.historyData
		// }
	},
	directives: {}
}
